// BankingApp.js
import React, { useState, useEffect } from 'react';
import { ref, onValue } from 'firebase/database';
import Cookies from 'js-cookie';
import LottieLoader from './LottieLoader';
import Authentication from './Authentication';
import Chatbox from './Chatbox';
import MessageList from './MessageList';
import UserDashboard from './UserDashboard';  // Importing the UserDashboard component

import { database } from './firebaseSetup';

function BankingApp() {
    const [username, setUsername] = useState('');
    const [registerEmail, setRegisterEmail] = useState('');
    const [balance, setBalance] = useState(0);
    const [registerPassword, setRegisterPassword] = useState('');
    const [hasProfile, setHasProfile] = useState(false);
    const [message, setMessage] = useState('');
    const [messages, setMessages] = useState([]);
    const [isRegistered, setIsRegistered] = useState(false);
    const [isEmailSet, setIsEmailSet] = useState(false);

    // ... Your useEffect and other logic ...
    useEffect(() => {
        // Fetch the current user's balance from the database when the component mounts
        const balanceRef = ref(database, 'balances/' + username);
        onValue(balanceRef, (snapshot) => {
            setBalance(snapshot.val() || 0);
        });
    }, [username]);

    return (
        <div>
            <LottieLoader />
            {isEmailSet && isRegistered ? (
                <div>
                    <h2>Welcome, {username}!</h2>
                    <UserDashboard username={username} balance={balance} setBalance={setBalance} />  {/* Using the UserDashboard component */}
                    <MessageList messages={messages} />
                    <Chatbox username={username} message={message} setMessage={setMessage} />
                </div>
            ) : (
                <Authentication 
                    setUsername={setUsername} 
                    setIsRegistered={setIsRegistered} 
                    setIsEmailSet={setIsEmailSet} 
                    registerEmail={registerEmail} 
                    setRegisterEmail={setRegisterEmail} 
                    registerPassword={registerPassword} 
                    setRegisterPassword={setRegisterPassword} 
                    username={username}
                />
            )}
        </div>
    );
}

export default BankingApp;
