// SendMoneyForm.js
import React, { useState } from 'react';
import { ref, onValue, set } from 'firebase/database';
import { database } from './firebaseSetup';

function SendMoneyForm({ username, balance, setBalance }) {
    const [recipient, setRecipient] = useState('');
    const [amountToSend, setAmountToSend] = useState(0);

    const handleSendMoney = () => {
        if (amountToSend > balance) {
            alert("You don't have enough balance to send this amount.");
            return;
        }

        // Deduct from sender's balance
        const senderBalanceRef = ref(database, 'balances/' + username);
        set(senderBalanceRef, balance - amountToSend);

        // Add to recipient's balance
        const recipientBalanceRef = ref(database, 'balances/' + recipient);
        onValue(recipientBalanceRef, (snapshot) => {
            const recipientBalance = snapshot.val() || 0;
            set(recipientBalanceRef, recipientBalance + amountToSend);
        });

        alert("Money sent successfully!");
    };

    return (
        <div>
            <input 
                type="text" 
                placeholder="Recipient's Username" 
                value={recipient}
                onChange={(e) => setRecipient(e.target.value)}
            />
            <input 
                type="number" 
                placeholder="Amount to Send" 
                value={amountToSend}
                onChange={(e) => setAmountToSend(e.target.value)}
            />
            <button onClick={handleSendMoney}>Send Money</button>
        </div>
    );
}

export default SendMoneyForm;
